import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import Mouse from '../components/Mouse'
import '../styles/pages/Step_1.sass'

const Step_1_Camera = () => {
  return (
    <main>
      <TutorialContainer>
        <div className="Step_1">
          <Caption>
            <img className="Step_1__Icon--Camera" src="./assets/icons/icon-camera.svg" alt=""/>
            <span className="Caption--Regular">Camera</span>
            <span className="Caption--Bold">Movement</span>
          </Caption>
          <div className="Step_1__Image-Container">
            <img className='Step_1__Image' src="./assets/images/apartment-illustration.svg" alt="" />
            <Mouse/>
          </div>
        </div>

        <NextButton nextStep={2} timeToAppear={10}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_1_Camera