import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import '../styles/pages/Step_7.sass'

const Step_7_Shelves = () => {
  return (
    <main>
      <TutorialContainer>
        <div className='Step_7'>
          <button className='Step_7__Button'>
            <img className="Step_7__Icon" src="./assets/icons/icon-sofa.svg" alt="" />
          </button>
          <button className='Step_7__Button'>
            <img className="Step_7__Icon" src="./assets/icons/icon-floor-config.svg" alt="" />
          </button>
          <button className='Step_7__Button--Cyan'>
            <img className="Step_7__Icon" src="./assets/icons/icon-shelves.svg" alt="" />
          </button>

          <Caption>
            <img className='Step_7__Arrow' src="./assets/icons/icon-arrow-config.svg" alt=""/>
            <span className="Caption--Regular">Customize</span>
            <span className="Caption--Bold">Shelves Color</span>
          </Caption>
        </div>

        <NextButton nextStep={8} timeToAppear={1}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_7_Shelves