import { useState } from 'react'
import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import Mouse from '../components/Mouse'
import '../styles/pages/Step_9.sass'
import { useEffect } from 'react'

const Step_9_Select = () => {
  const [ changeSofa, setChangeSofa ] = useState(false)

  useEffect(() => {
    setTimeout(() => {
      setChangeSofa(true)
    }, [6500])
  }, [changeSofa])

  return (
    <main>
      <TutorialContainer>
        <div className='Step_9'>
          <button className='Step_9__Button'>
            <img className="Step_9__Icon" src="./assets/icons/icon-sofa.svg" />
          </button>
          <button className='Step_9__Button'>
            <img className="Step_9__Icon" src="./assets/icons/icon-floor-config.svg" />
          </button>
          <button className='Step_9__Button'>
            <img className="Step_9__Icon" src="./assets/icons/icon-shelves.svg" />
          </button>

          <div className='Config-Items__Container--One-Disappear'>
            <img className='Config__Item' src="./assets/colors/blue.svg"/>
            <img className='Config__Item' src="./assets/colors/green.svg"/>
            <img className='Config__Item' src="./assets/colors/white.svg"/>
          </div>
          <div className='Config-Items__Container--Two-Disappear'>
            <img className='Config__Item' src="./assets/colors/texture-1.png"/>
            <img className='Config__Item' src="./assets/colors/texture-2.png"/>
            <img className='Config__Item' src="./assets/colors/texture-3.png"/>
          </div>
          <div className='Config-Items__Container--Three-Disappear'>
            <img className='Config__Item' src="./assets/colors/brown.svg"/>
            <img className='Config__Item' src="./assets/colors/black.svg"/>
            <img className='Config__Item' src="./assets/colors/dark-brown.svg"/>
          </div>
        </div>

        <div className='Sofa-Container'>
          <img className='Sofa-1' src={`${changeSofa ? './assets/images/sofa-2.png' : './assets/images/sofa-1-green.png'}`} alt="" />
          <Caption>
            <img className='Config__Arrow' src="./assets/icons/icon-arrow-config.svg" alt=""/>
            <div>
              <span className="Caption--Regular">Click on the furniture to</span>
              <span className="Caption--Bold">select it</span>
            </div>
          </Caption>
          <Mouse/>
        </div>

        <div className='Change-Sofa__Container'>
          <button className='Change-Sofa__Button'>
            <img className='Change-Sofa__Icon' src="./assets/icons/icon-change-sofa.svg" alt="" />
          </button>

          <span className='Change-Sofa__Text'>{`${changeSofa ? 'Tempo Design' : 'Furniture 1'}`}</span>

          <Caption>
            <img className='Config__Arrow' src="./assets/icons/icon-arrow-config.svg" alt=""/>
            <span className="Caption--Regular">When you select the furniture, the icon <span className="Caption--Bold-Cyan">turns blue</span>
                . Press it to change the type</span>
          </Caption>
          <Mouse/>

          <button className="Change-Sofa__Add-Button">
            <img className='Change-Sofa__Cruz' src="./assets/icons/icon-cruz.svg" alt="" />
          </button>
        </div>

        <NextButton nextStep={10} timeToAppear={8}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_9_Select