import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import Mouse from '../components/Mouse'
import '../styles/pages/Step_4.sass'

const Step_4_Floor_Navigation = () => {
  return (
    <main>
      <TutorialContainer>
        <div className="Step_4">
          <Caption>
            <img className="Step_4__Icon--Floor" src="./assets/icons/icon-floor.svg" alt=""/>
            <span className="Caption--Regular">Floor</span>
            <span className="Caption--Bold">Navigation</span>
          </Caption>
          <div className="Step_4__Image-Container">
            <img className='Step_4__Image' src="./assets/images/apartment-illustration.svg" alt="" />
            <img className='Step_4__Circle' src="./assets/icons/icon-floor-circle.svg" alt="" />
            <Mouse/>
          </div>
        </div>

        <NextButton nextStep={5} timeToAppear={10}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_4_Floor_Navigation