import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import '../styles/pages/Step_2.sass'

const Step_2_Navigation_1 = () => {
  return (
    <main>
      <TutorialContainer>
        <div className="Step_2">
          <div className='Step_2__Content'>
            <Caption>
              <img className="Step_2__Icon--Keyboard" src="./assets/icons/icon-keyboard.svg" alt=""/>
              <span className="Caption--Bold">Navigation</span>
            </Caption>
            <div className="Step_2__Image-Container">
              <img className='Step_2__Image' src="./assets/images/apartment-illustration.svg" alt="" />
            </div>
          </div>
          <div className='Step_2__Key-Container'>
            <div className='Step_2__Key'>
              <img src="./assets/icons/icon-arrow-top.svg" alt="" />
            </div>
            <div className='Step_2__Key'>
              <img src="./assets/icons/icon-arrow-left.svg" alt="" />
            </div>
            <div className='Step_2__Key'>
              <img src="./assets/icons/icon-arrow-bottom.svg" alt="" />
            </div>
            <div className='Step_2__Key'>
              <img src="./assets/icons/icon-arrow-right.svg" alt="" />
            </div>

            <span className='Step_2__Key-Text'>Option 1</span>
          </div>
        </div>

        <NextButton nextStep={3} timeToAppear={10}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_2_Navigation_1