import { useState, useEffect, useRef } from 'react'
import { Link } from 'react-router-dom'
import '../styles/pages/Home.sass'

const Home = () => {
  const [ hide, setHide ] = useState(false)
  const [ enterRoom, setEnterRoom ] = useState(false)
  const enterRoomRef = useRef(null)
  const loadingRef = useRef(null)
  const menuRef = useRef(null)

  useEffect(() => {
    setTimeout(() => {
      setHide(true)
    }, 7000)
  }, [])

  const handleClick = () => {
    setEnterRoom(true)
    setTimeout(() => {
      enterRoomRef.current.classList.add('hide')
    }, 1250)
    setTimeout(() => {
      loadingRef.current.classList.add('hide')
    }, 2000)
    setTimeout(() => {
      menuRef.current.classList.remove('hide')
    }, 2000)
  }

  return (
    <main>
      <div className="Loading__Container" ref={loadingRef}>
        <div className={`Loading-Initial ${hide && 'hide'}`}>
          <div className="Loading-Initial__Image-Container">
            <img className='Loading-Initial__Image-Icon' src="../assets/images/wireframe-icon.svg" alt="wireframe"/>
            <img className='Loading-Initial__Image-Text' src="../assets/images/wireframe-text.svg" alt="wireframe"/>
          </div>

          <div className="Loading-Initial__Bar">
            <div className="Loading-Initial__Bar-Item"></div>
            <div className="Loading-Initial__Bar-Item"></div>
            <div className="Loading-Initial__Bar-Item"></div>
            <div className="Loading-Initial__Bar-Item"></div>
            <div className="Loading-Initial__Bar-Item"></div>
            <div className="Loading-Initial__Bar-Item"></div>
            <div className="Loading-Initial__Bar-Item"></div>
          </div>
        </div>

        <div className={`${enterRoom ? 'Loading-Finish--Animated' : 'Loading-Finish'} ${!hide && 'hide'}`}>
          <div className="Loading-Finish__Logo-Container">
            <img className='Loading-Finish__Logo' src="../assets/images/studio-apartment-logo.svg" alt="" />
          </div>
          <div className="Loading-Finish__Visor-Container">
            <img className='Loading-Finish__Visor-Image' src="../assets/images/background.jpg" alt="" />
          </div>
          <button className='Loading-Finish__Button' ref={enterRoomRef} onClick={handleClick}>
            <div className='Loading-Finish__Button-Text'>
              <div>ENTER</div>
              <div>ROOM</div>
            </div>
          </button>
          <span className='Loading-Finish__Text'>By: Wireframe Reality</span>
        </div>
      </div>
      <div className='Menu__Container hide' ref={menuRef}>
        <div className="Menu">
          <Link className="Menu__Button--Cyan" to='/tutorial-1'>Instructions</Link>
          <button className="Menu__Button--Gray">Start</button>
        </div>
      </div>
    </main>
  )
}

export default Home