import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import '../styles/pages/Step_3.sass'

const Step_3_Navigation_2 = () => {
  return (
    <main>
      <TutorialContainer>
        <div className="Step_3">
          <div className='Step_3__Content'>
            <Caption>
              <img className="Step_3__Icon--Keyboard" src="./assets/icons/icon-keyboard.svg" alt=""/>
              <span className="Caption--Bold">Navigation</span>
            </Caption>
            <div className="Step_3__Image-Container">
              <img className='Step_3__Image' src="./assets/images/apartment-illustration.svg" alt="" />
            </div>
          </div>
          <div className='Step_3__Key-Container'>
            <div className='Step_3__Key'>
                W
            </div>
            <div className='Step_3__Key'>
                A
            </div>
            <div className='Step_3__Key'>
                S
            </div>
            <div className='Step_3__Key'>
                D
            </div>

            <span className='Step_3__Key-Text'>Option 2</span>
          </div>
        </div>

        <NextButton nextStep={4} timeToAppear={10}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_3_Navigation_2