import PropTypes from 'prop-types'
import '../styles/components/Caption.sass'

const Caption = ({ children, number = 1 }) => {
  return (
    <div className={number === 2 ? 'Caption-2' : 'Caption'}>
      { children }
    </div>
  )
}

Caption.propTypes = {
  children: PropTypes.node.isRequired,
  number: PropTypes.number
}

export default Caption