import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import '../styles/pages/Step_5.sass'

const Step_5_Sofa = () => {
  return (
    <main>
      <TutorialContainer>
        <div className='Step_5'>
          <button className='Step_5__Button--Cyan'>
            <img className="Step_5__Icon" src="./assets/icons/icon-sofa.svg" alt="" />
          </button>
          <button className='Step_5__Button'>
            <img className="Step_5__Icon" src="./assets/icons/icon-floor-config.svg" alt="" />
          </button>
          <button className='Step_5__Button'>
            <img className="Step_5__Icon" src="./assets/icons/icon-shelves.svg" alt="" />
          </button>

          <Caption>
            <img className='Step_5__Arrow' src="./assets/icons/icon-arrow-config.svg" alt=""/>
            <span className="Caption--Regular">Customize</span>
            <span className="Caption--Bold">Sofa Color</span>
          </Caption>
        </div>

        <NextButton nextStep={6} timeToAppear={1}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_5_Sofa