import { BrowserRouter, Routes, Route } from 'react-router-dom'
import Home from '../pages/Home'
import Step_1_Camera from '../pages/Step_1_Camera'
import Step_2_Navigation_1 from '../pages/Step_2_Navigation_1'
import Step_3_Navigation_2 from '../pages/Step_3_Navigation_2'
import Step_4_Floor_Navigation from '../pages/Step_4_Floor_Navigation'
import Step_5_Sofa from '../pages/Step_5_Sofa'
import Step_6_Floor from '../pages/Step_6_Floor'
import Step_7_Shelves from '../pages/Step_7_Shelves'
import Step_8_Color from '../pages/Step_8_Color'
import Step_9_Select from '../pages/Step_9_Select'
import Step_10_Add_Sofa from '../pages/Step_10_Add_Sofa'
import Step_11_Delete_Sofa from '../pages/Step_11_Delete_Sofa'

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route exact path="/" element={<Home/>}/>
        <Route exact path="/tutorial-1" element={<Step_1_Camera/>}/>
        <Route exact path="/tutorial-2" element={<Step_2_Navigation_1/>}/>
        <Route exact path="/tutorial-3" element={<Step_3_Navigation_2/>}/>
        <Route exact path="/tutorial-4" element={<Step_4_Floor_Navigation/>}/>
        <Route exact path="/tutorial-5" element={<Step_5_Sofa/>}/>
        <Route exact path="/tutorial-6" element={<Step_6_Floor/>}/>
        <Route exact path="/tutorial-7" element={<Step_7_Shelves/>}/>
        <Route exact path="/tutorial-8" element={<Step_8_Color/>}/>
        <Route exact path="/tutorial-9" element={<Step_9_Select/>}/>
        <Route exact path="/tutorial-10" element={<Step_10_Add_Sofa/>}/>
        <Route exact path="/tutorial-11" element={<Step_11_Delete_Sofa/>}/>
      </Routes>
    </BrowserRouter>
  )
}

export default App

