import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import '../styles/pages/Step_6.sass'

const Step_6_Floor = () => {
  return (
    <main>
      <TutorialContainer>
        <div className='Step_6'>
          <button className='Step_6__Button'>
            <img className="Step_6__Icon" src="./assets/icons/icon-sofa.svg" alt="" />
          </button>
          <button className='Step_6__Button--Cyan'>
            <img className="Step_6__Icon" src="./assets/icons/icon-floor-config.svg" alt="" />
          </button>
          <button className='Step_6__Button'>
            <img className="Step_6__Icon" src="./assets/icons/icon-shelves.svg" alt="" />
          </button>

          <Caption>
            <img className='Step_6__Arrow' src="./assets/icons/icon-arrow-config.svg" alt=""/>
            <span className="Caption--Regular">Customize</span>
            <span className="Caption--Bold">Floor Type</span>
          </Caption>
        </div>

        <NextButton nextStep={7} timeToAppear={1}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_6_Floor