import { useState, useEffect } from 'react'
import TutorialContainer from '../components/TutorialContainer'
import NextButton from '../components/NextButton'
import Caption from '../components/Caption'
import Mouse from '../components/Mouse'
import '../styles/pages/Step_8.sass'

const Step_8_Color = () => {
  const [ sofaGreen, setSofaGreen ] = useState(false)

  useEffect(() => {
    setTimeout(() => {
      setSofaGreen(true)
    }, [9000])
  }, [sofaGreen])

  return (
    <main>
      <TutorialContainer>
        <div className='Step_8'>
          <button className='Step_8__Button'>
            <img className="Step_8__Icon" src="./assets/icons/icon-sofa.svg" />
          </button>
          <button className='Step_8__Button'>
            <img className="Step_8__Icon" src="./assets/icons/icon-floor-config.svg" />
          </button>
          <button className='Step_8__Button'>
            <img className="Step_8__Icon" src="./assets/icons/icon-shelves.svg" />
          </button>

          <Caption>
            <img className='Step_8__Arrow' src="./assets/icons/icon-arrow-config.svg"/>
            <span className="Caption--Regular">Click to</span>
            <span className="Caption--Bold">see the available options</span>
          </Caption>
          <Caption number={2}>
            <img className='Step_8__Arrow' src="./assets/icons/icon-arrow-config.svg"/>
            <span className="Caption--Regular">Click here to</span>
            <span className="Caption--Bold">change the colors/textures</span>
          </Caption>

          <Mouse/>

          <div className='Config-Items__Container--One'>
            <img className='Config__Item' src="./assets/colors/blue.svg" />
            <img className='Config__Item' src="./assets/colors/green.svg" />
            <img className='Config__Item' src="./assets/colors/white.svg" />
          </div>
          <div className='Config-Items__Container--Two'>
            <img className='Config__Item' src="./assets/colors/texture-1.png" />
            <img className='Config__Item' src="./assets/colors/texture-2.png" />
            <img className='Config__Item' src="./assets/colors/texture-3.png" />
          </div>
          <div className='Config-Items__Container--Three'>
            <img className='Config__Item' src="./assets/colors/brown.svg" />
            <img className='Config__Item' src="./assets/colors/black.svg" />
            <img className='Config__Item' src="./assets/colors/dark-brown.svg" />
          </div>
        </div>

        <div className='Sofa-Container'>
          <img className='Sofa-1' src={`${sofaGreen ? './assets/images/sofa-1-green.png' : './assets/images/sofa-1-white.png'}`} alt="" />
        </div>

        <NextButton nextStep={9} timeToAppear={10}/>
      </TutorialContainer>
    </main>
  )
}

export default Step_8_Color